<template>
  <div class="exChange-box">
    <div class="chang">
      <div class="head-box">
        <div class="exchange-title">{{language.label_5_3}}</div>
        <div class="token">
          <div class="token-txt"> {{language.exchange_tokens}}</div>
          <div class="token-img">
            <img
              @click="setUpFn"
              src="../../images/market/zhineng.png"
              alt=""
            />
            <img src="../../images/market/dui.png" alt="" />
          </div>
        </div>
      </div>

      <div class="exchange-list">
        <div class="balance">
          <div> {{language.once_upon_time}}</div>
          <div>{{language.wallet_balance}}: {{    inputBlane   }}</div>
        </div>

        <div class="btck-box">
          <div class="coloSty">
            <input
              type="text"
              class="my-input"
              v-model="inputAmount" 
              v-on:input="onChangeInput"
              placeholder="0.00"
            />
          </div>
          <div class="max" @click="maxiMumFn">MAX</div>
          <div @click="popupCurrency(0)">
            <img :src="listCurrency[indexGo].img" alt="" />
            <span class="coloSty">{{ listCurrency[indexGo].currency }}</span>
            <img src="../../images/option/arrow_down@3x.png" alt="" />
          </div>
        </div>
      </div>
    
       <div class="mobility">
            <span v-if="statusIsShow"> {{language.transaction_is_illiquid}}</span>
       </div>

      <div class="toggle" @click="switchFn">
        <img src="../../images/market/qieh.png" alt="" />
      </div>

      <div class="exchange-list">
        <div class="balance">
           <div> {{language.once_upon_time}}</div>
          <div >{{language.wallet_balance}}：  {{ outputBlane }} </div>
        </div>
        <div class="btck-box" >
          <div class="coloSty">  
                <input
                type="text"
                class="my-input"
                v-model="outputAmount"
                v-on:input="onChangeOut"
                placeholder="0.00"
            />
          </div>
          <div @click="popupCurrency(1)">
            <img :src="listCurrency[indexTo].img" alt="" />
            <span class="coloSty">{{ listCurrency[indexTo].currency }}</span>
            <img src="../../images/option/arrow_down@3x.png" alt="" />
          </div>
        </div>
      </div>

      <div class="footer">
        <div class="footer-left">{{info.executionPrice}} {{ listCurrency[indexTo].currency }}  per {{ listCurrency[indexGo].currency }}</div>

        <div class="footer-list" v-if="tradeType =='0'">
          <div> {{language.minimum_gain}} <img src="../../images/farm/help.png" alt="" /></div>
          <p>{{info.minimumAmountOut}} {{ listCurrency[indexTo].currency }}</p>
        </div>
       
         <div class="footer-list" v-else>
          <div>{{language.max_payout}} <img src="../../images/farm/help.png" alt="" /></div>
          <p>{{info.maximumAmountIn}} {{ listCurrency[indexGo].currency }}</p>
        </div>


        <div class="footer-list">
          <div>{{language.price_impact}} <img src="../../images/farm/help.png" alt="" /></div>
          <p>&lt;{{info.priceImpact}}</p>
        </div>

        <div class="footer-list">
          <div>{{language.provider_fee}} <img src="../../images/farm/help.png" alt="" /></div>
          <p>{{info.fee}} BNB</p>
        </div>


      </div>

      <m-button style="font-size:16px" @click="warrantFn" v-if="!allowance"  class="exchange btn  hdr-el-blue "  :loading="reapLoading"
             :height="40">
             {{language.btn_authorize}}
      </m-button>
       <m-button style="font-size:16px"   @click="swapFn" v-else class="exchange btn  hdr-el-blue "  :loading="reapLoading"
            :height="40">
            {{language.label_5_3}}
      </m-button>

  
    </div>

    <m-popper v-model="showSelection">
      <template v-slot:title>{{language.history_s}}</template>
      <div class="props-boxs">
        <div class="tolerance">
          {{language.slippage_tolerance}} <img
            class="icon-img"
            src="../../images/lottery/what@2x.png"
            alt=""
          />
        </div>
        <div class="choice">
          <div @click="jebeFn(1)" :class="indexStatus == 1 ? 'jebeBg' : ''">
            0.1%
          </div>
          <div @click="jebeFn(2)" :class="indexStatus == 2 ? 'jebeBg' : ''">
            0.5%
          </div>
          <div @click="jebeFn(3)" :class="indexStatus == 3 ? 'jebeBg' : ''">
            1%
          </div>
          <div @click="jebeFn(4)" :class="indexStatus == 4 ? 'jebeBg' : ''">
            <input type="text" v-model="slipPointValue" @input="inputChang" />
          </div>
          <div class="footer-choice">%</div>
        </div>

        <div class="tolerance">
          {{language.trade_deadline}}<img
            class="icon-img"
            src="../../images/lottery/what@2x.png"
            alt=""
          />
        </div>

        <div class="time-deadline">
          <input type="text"  v-model="ttl" /> <span>{{language.minute_f}}</span>
        </div>
      </div>
    </m-popper>

    <currency
      :isRepeatGo="isRepeatGo"
      :isRepeat="isRepeat"
      :currencyStatus="currencyStatus"
      @currencyFn="currencyFn"
    ></currency>
  </div>
</template>
<script>
import MSwitch from "@/components/mSwitch";
import MPopper from "@/components/mPopper";
import currency from "./popUpLayer/currency";
import { mapGetters ,mapState} from "vuex";
import MButton from "@/components/mButton";
import { BN } from "@/common/util";
import { ethers } from "ethers";
//import { getDefaultProvider } from "@ethersproject/providers"
import { balanceFormatter} from "@/common/dataConvertor";
import { listCurrency ,coinMap} from "@/common/walletConfig"
import {
  ChainId,
  Token,
  Percent,
  Fetcher,
  Trade,
  Router,
  TokenAmount,
  TradeType,
} from "honcur-pancakeswap-sdk";
const chain = ChainId.TESTNET;
const WBNB = new Token(chain, "0xae13d989daC2f0dEbFf460aC112a837C89BAa7cd", 18);

const provider = new ethers.providers.Web3Provider(window.ethereum);
//let provider = getDefaultProvider('https://data-seed-prebsc-1-s1.binance.org:8545/');
export default {
  components: {
    MPopper,
    currency,
    MSwitch,
    MButton
  },
  computed: {
    ...mapState("app", ["address","balance"]),
    ...mapState("nft", ["myCards"]),
    ...mapGetters(["language"]),
   
  },
  data() {
    return {
      statusIsShow:false,
        listCurrency,
        coinMap,
      slipPointValue: 0.5,
      isRepeatGo:"",
      isRepeat: "",
      showSelection: false,
      currencyStatus: false,
      hedged: "",
      hedgeds: "",
      indexStatus: 2,
      indexGo: 0,
      indexTo: 1,
      onuIndex: null,
      onIndex: null,
      info: {
       inputAmount: 0, //购买支出的数量
       outputAmount: 0, //购买得到的数量
       executionPrice: 0, //价格
       invertPrice: 0, //反向价格
       priceImpact: 0, //价格影响
       fee: 0, //手续费 提供者费用
       minimumAmountOut: 0, //最小获得数量(精确购买需要支出的数量展示 tradeType == TradeType.EXACT_INPUT )
       maximumAmountIn: 0, //最大支出数量(精确购买得到的数量展示 tradeType == TradeType.EXACT_OUTPUT)
      },
      inputAddress: "0x78867bbeef44f2326bf8ddd1941a4439382ef2a7", //用什么代币购买（合约地址）默认DFT
      inputAmount: 0, //准备提供多少数量去购买
      outputAddress: "0x8babbb98678facc7342735486c851abd7a0d17ca", //购买什么代币（合约地址） 默认WBNB
      outputAmount: 0, //购买多少数量
      // pointValue
      slippage: 50, //滑点 默认：0.5%
      ttl: 30, //交易时长,
      tradeType: TradeType.EXACT_INPUT, //交易类型
      contract: null,
      allowance: false,
      reapLoading:false,
      currencyData:"ETH",//下面的值
      currencyDataGo:"BUSD", //币值 
      inputBlane:"",
      outputBlane:"",
    };
  },
  async mounted() {
     provider.send('eth_requestAccounts', []) //这个别删除
    // 判断是否授权
      this.isApprovedFn();
      this.balanceInput(this.inputAddress);
      this.balanceoutput(this.outputAddress);
  },
  watch: {
    inputAddress: function(o,v) {
      this.onChangeInputAddress();
      this.balanceInput(o);
      console.log(o,v)
    },
    outputAddress(o,v){
        this.balanceoutput(o);
        console.log(o,v)
    },
  },
  methods: {
    balanceFormatter,
    // 点击max 
    maxiMumFn(){
       this.$api.erc20Balance(this.inputAddress).then((res)=>{
           let out =  new BN(res).toDisplay(18).toFixed(4)
           this.inputAmount = out;
           this.onChangeInput();
       })
    },
    balanceInput(data){
       this.$api.erc20Balance(data).then((res)=>{
            this.inputBlane  =  new BN(res).toDisplay(18).toFixed(4)
       })
    },
     balanceoutput(data){
       this.$api.erc20Balance(data).then((res)=>{
            this.outputBlane  =  new BN(res).toDisplay(18).toFixed(4)
       })
    },
    // 判断是否授权
    isApprovedFn() {
      console.info(`this.inputAddress = ${this.inputAddress}`)
      this.$api.erc20AllowanceForAddress("IUniswapV2Router02", this.inputAddress).then((res) => {
        this.allowance = Number(res);
      });
    },
      // 授权
    warrantFn() {
      this.reapLoading = true
      this.$api.erc20ApproveForAddress("IUniswapV2Router02",this.inputAddress).then(() => {
        this.reapLoading = false
        this.allowance = true;
      });
    },
async getInputTrade(inputAddress, outoutAddress, amountIn) {
      const inputToken = new Token(chain, inputAddress, 18);
      const outputToken = new Token(chain, outoutAddress, 18);
      let pairs = [];
      console.info('provider' +  provider)
      await Fetcher.fetchPairData(inputToken, outputToken, provider).then((pair) => {
        pairs.push(pair);
      }).catch( async (e)=> {
          console.info(e)
          const pair = await Fetcher.fetchPairData(WBNB, inputToken, provider)
          const pair1 = await Fetcher.fetchPairData(WBNB, outputToken, provider)
          pairs.push(pair)
          pairs.push(pair1)
      }).catch((e) => {
         console.info("e1" + e)
      });
      if(pairs.length  == 0) {
        return 
      }
      let trades = Trade.bestTradeExactIn(pairs,  new TokenAmount(inputToken, amountIn), outputToken);
       console.info("---------------" + trades.length)
       if(trades.length > 0) {
        return trades[0];
      }
    },

      async getOutputTrade(inputAddress, outoutAddress, amountOut) {
      const inputToken = new Token(chain, inputAddress, 18);
      const outputToken = new Token(chain, outoutAddress, 18);
      let pairs = [];
      await Fetcher.fetchPairData(inputToken, outputToken, provider).then((pair) => {
         pairs.push(pair);
      }).catch( async ()=> {
          const pair = await Fetcher.fetchPairData(WBNB, inputToken, provider)
          const pair1 = await Fetcher.fetchPairData(WBNB, outputToken, provider)
          pairs.push(pair)
          pairs.push(pair1)
      }).catch(() => {});
      let trades = Trade.bestTradeExactOut(pairs, inputToken , new TokenAmount(outputToken, amountOut));
        console.info("---------------" + trades.length)
       if(trades.length > 0) {
        return trades[0];
      }
    },


      //设置页面的交易信息数据
    async setTradeAndInfo(inputAddress, outoutAddress, amount, slippage, tradeType) {
      console.log(inputAddress, outoutAddress, amount, slippage, tradeType)
      const slippageTolerance = new Percent(slippage, "10000"); 
      let  trade;
      if(tradeType == TradeType.EXACT_INPUT) {
        trade = await this.getInputTrade(inputAddress, outoutAddress, amount.toString());
     } else {
         trade = await this.getOutputTrade(inputAddress, outoutAddress, amount.toString());
      }
      if(trade) {
            this.statusIsShow = false;
            this.info.maximumAmountIn =  trade.maximumAmountIn(slippageTolerance).toFixed(8);
            this.info.minimumAmountOut =  trade.minimumAmountOut(slippageTolerance).toFixed(8);
            this.info.inputAmount =  trade.inputAmount.toFixed(8);
            this.info.priceImpact = trade.priceImpact.toFixed(8);
            this.info.outputAmount =  trade.outputAmount.toFixed(8);
            this.info.executionPrice = trade.executionPrice.toFixed(8);
            this.info.invertPrice = trade.executionPrice.invert().toFixed(8);
            this.info.priceImpact =  trade.priceImpact.toFixed(8);
            this.info.fee = trade.outputAmount.multiply(3).divide(1000).toFixed(8);
            if(tradeType == TradeType.EXACT_INPUT) {
              this.outputAmount = trade.outputAmount.toFixed(8);
            } else {
                this.inputAmount = trade.inputAmount.toFixed(8);
            }
      }else{
         this.statusIsShow = true;
      }
      console.info(  this.inputAddress + "__" + this.outputAddress  + "____" +   this.inputAmount + "______" + this.outputAmount )
    },

   async onChangeInput() {
     console.log(this.inputAddress)
        let amount = new BN(this.inputAmount);//用BN!!!!
        this.tradeType = TradeType.EXACT_INPUT
        console.info(amount.toWei("balance").toString())
        await  this.setTradeAndInfo(this.inputAddress, this.outputAddress, amount.toWei("balance").toString(10), this.slippage, TradeType.EXACT_INPUT);
    },
 async onChangeOut() {
       let amount = new BN(this.outputAmount);//用BN!!!!
       this.tradeType = TradeType.EXACT_OUTPUT
       await  this.setTradeAndInfo(this.inputAddress, this.outputAddress, amount.toWei("balance").toString(10), this.slippage, TradeType.EXACT_OUTPUT);
    },

    async onChangeInputAddress() {
          this.isApprovedFn();
    },

 async swap(inputAddress, outoutAddress, amount, slippage, tradeType, ttl) {
      const allowedSlippage = new Percent(slippage, "10000");
      let recipient = this.address; //用户地址,
      let parameter;
      if(tradeType == TradeType.EXACT_INPUT) {
        let trade = await this.getInputTrade(inputAddress, outoutAddress, amount);
        let tradeOptions ={allowedSlippage, ttl, recipient};
       parameter = Router.swapCallParameters(trade, tradeOptions);
      } else {
        let trade = await this.getOutputTrade(inputAddress, outoutAddress, amount);
        let tradeOptions ={allowedSlippage, ttl, recipient };
         parameter = Router.swapCallParameters(trade, tradeOptions);
      }
      this.reapLoading = true;
      this.$api.callSend(parameter).then(()=>{
          this.reapLoading = false;
          this.inputAmount = 0;
          this.outputAmount = 0;
          this.info.maximumAmountIn = "";
          this.info.minimumAmountOut = "";
          this.info.inputAmount = "";
          this.info.priceImpact = "";
          this.info.outputAmount =  "";
          this.info.executionPrice = "";
          this.info.invertPrice = "";
          this.info.priceImpact =  "";
          this.info.fee = "";
      }).catch(()=>{
         this.reapLoading = false;
      });
    },
    async swapFn() {
      if(!this.inputAmount){
         return 
      }
       let amount = 0;
       if(this.tradeType == TradeType.EXACT_INPUT) {
         amount = new BN(this.inputAmount).toWei("balance");
       } else {
          amount = new BN(this.inputAmount).toWei("balance");
       }
       await this.swap(this.inputAddress, this.outputAddress, amount, this.slippage, this.tradeType, this.ttl * 60 );
    },
    // 选择滑点
    jebeFn(index) {
      if (index == 1) {
        this.slippage = 10;
        this.slipPointValue = 0;
      } else if (index == 2) {
        this.slippage =50;
         this.slipPointValue = 0;
      } else if (index == 3) {
        this.slippage = 100;
        this.slipPointValue = 0;
      }
      if (index == 4) {
        this.slippage = this.slipPointValue*100 ;
      }
      this.indexStatus = index;
    },
    // input chang事件
    inputChang(){
          this.slippage = this.slipPointValue*100 ;
    },
    setUpFn() {
      this.showSelection = true;
    },
    // 切换
    switchFn() {
        [this.listCurrency[this.indexGo],this.listCurrency[this.indexTo]] = [this.listCurrency[this.indexTo],this.listCurrency[this.indexGo]];
        let temp = this.inputAddress;
        this.inputAddress = this.outputAddress;
        this.outputAddress = temp;
    },
    // 点击弹出选择交易对
    popupCurrency(index) {
      this.formatData = index;
      this.currencyStatus = true;
      this.isRepeat = this.listCurrency[this.indexTo];
      this.isRepeatGo = this.listCurrency[this.indexGo];
    },
    // 子组件方法
    currencyFn(item, index) {
      if (item && this.formatData) {
        this.currencyData = item.currency;
        this.outputAddress = item.id;
        this.indexTo = index;
      } else {
        if(item){
             this.statusIsShow = false;
             this.inputAmount = "";
             this.currencyDataGo = item.currency;
             this.inputAddress = item.id; 
             this.indexGo = index;
          } else{
              this.currencyStatus = false;
        }
        this.currencyStatus = false;
      }
       this.currencyStatus = false;
      
    },
  },
};
</script>
