<template>
  <div class="dialog" v-show="currencyStatus">
    <div class="dialog-container">
      <div class="dialog-title">{{language.choose_token}}</div>

      <div class="close">
        <img src="../../../images/close@2x.png" alt="" @click="goBack()" />
      </div>


      <div class="currency-box">
        <div
        class="currency-list"
          :class=" isCurrency == index ||  isCurrencyGO == index  ? 'currency-list1' :''"
          v-for="(item, index) in list"
          :key="index"
          @click=" isCurrency == index ||  isCurrencyGO == index ? '' : getCurrencyFn(item,index) "
        >
          <div><img :src="item.img" alt="" /></div>
          <div class="curr">
            <div class="fon-s">{{item.currency}}</div>
            <div>{{item.currency}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import MButton from "@/components/mButton";
import { mapState, mapGetters } from "vuex";
import { listCurrency } from "@/common/walletConfig"

export default {
  components: { MButton },
  props: {
    currencyStatus: {
      type: Boolean,
      default: false,
    },
    isRepeat:{
        type: String,
        default: "",
    },
    isRepeatGo:{
        type: String,
        default: "",
    }
  },
  computed: {
    ...mapState("app", ["address"]),
    ...mapState("nft", ["myCards"]),
    ...mapGetters(["language"]),
    list(){
      return listCurrency
    }
  },
 
  data() {
    return {
        isCurrency:"",
       isCurrencyGO:"",
    };
  },
   watch:{
      isRepeat(old,v){
        console.log(old,v)
        for (let i = 0; i < this.list.length; i++) {
              if(old.currency == this.list[i].currency){
                  this.isCurrency = i
              }
         } 
      },
      isRepeatGo(old,v){
           console.log(old,v)
         for (let i = 0; i < this.list.length; i++) {
              if(old.currency == this.list[i].currency){
                  this.isCurrencyGO = i
              }
         } 
      }
  },
  mounted() {
       
  },
  methods: {
    giveFn() {
      this.$emit("currencyFn");
    },
    goBack() {
      this.$emit("currencyFn");
    },
    getCurrencyFn(item,index){
        this.$emit("currencyFn",item,index);
    },
  },
};
</script>
